import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userCheck: true,
    show: true,
    version: 'YH-W-1.0.0', // 版本号
    agentNo: '2800001', // 代理机构号
    active: 0,
    token: [],
    password: '',
    imgArray: [],
    level: {
      1: '会员',
      2: '银牌代理',
      3: '金牌代理',
      4: '区级代理',
      5: '市级代理',
      6: '省级代理',
      7: '运营合伙人'
    },
    upgrade: {
      1: '升级银卡',
      2: '升级金卡'
    },
    userList: [],
    moneyLimit: {},
    openId: '',
    phone: false,
    real: 0
  },
  mutations: {
    SET_USERCHECK (state, code) {
      state.userCheck = code
    },
    SET_TOKEN (state, token) {
      state.token = token
    },
    SET_IMG (state, imgArray) {
      state.imgArray = imgArray
    },
    Change_Password (state, code) {
      state.password = code
    },
    Change_Limit (state, code) {
      state.moneyLimit = code
    },
    SET_OPENID (state, code) {
      state.openId = code
    },
    IS_PHONE (state, code) {
      state.phone = code
    },
    SET_REAL (state, code) {
      state.real = code
    }
  },
  actions: {
    SetUserCheck ({
      commit,
      state
    }, code) {
      return new Promise((resolve, reject) => {
        commit('SET_USERCHECK', code)
      })
    },
    SetToken ({ // 存token
      commit,
      state
    }, code) {
      return new Promise((resolve, reject) => {
        commit('SET_TOKEN', code)
      })
    },
    SetReal ({ // 存token
      commit,
      state
    }, code) {
      return new Promise((resolve, reject) => {
        commit('SET_REAL', code)
      })
    },
    SetPassword ({ // 存密码
      commit,
      state
    }, code) {
      return new Promise((resolve, reject) => {
        commit('Change_Password', code)
      })
    },
    setMoney ({
      commit,
      state
    }, code) {
      return new Promise((resolve, reject) => {
        commit('Change_Limit', code)
      })
    },
    setOpenid ({
      commit,
      state
    }, code) {
      return new Promise((resolve, reject) => {
        commit('SET_OPENID', code)
      })
    },
    setPhone ({
      commit,
      state
    }, code) {
      return new Promise((resolve, reject) => {
        commit('IS_PHONE', code)
      })
    }
  },
  plugins: [createPersistedState({
    storage: window.localStorage
  })]

})
